/* App.css */

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.App {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  background-color: #040C10;
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

/* Global styles */
body {
  font-family: 'Raleway', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  font-weight: 300;
}

/* Header styles */
.background-video {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.page-header {
  display: flex;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(4, 12, 16, 1)), url('./assets/header-background.jpg'); /* Correct path to your image */
  background-size: cover;
}

.intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 2;
  color: #fff;
}

.intro .project-name {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  font-size: 36px;
  font-weight: 400;
  margin-bottom: 10px;
}

.intro .project-name.active {
  opacity: 1;
}

.intro .highlight {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  font-size: 48px;
  font-weight: 700;
  margin-left: 10px;
  color: #89CFEF;
}

.intro .highlight.active {
  opacity: 1;
}

.intro .project-tagline {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  font-size: 24px;
  font-weight: 300;
  margin-bottom: 20px;
}

.intro .project-tagline.active {
  opacity: 1;
}

.intro .btn {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  display: inline-block;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  background-color: #1C2951;
  color: #fff;
  border-radius: 10px;
  transition: background-color 1s ease;
}

.intro .btn.active {
  opacity: 1;
}

.intro .btn:hover {
  background-color: #c1440e;
  cursor: none;
}

/* Navigation styles */
nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 3;
  transition: top 0.3s ease;
}

nav.sticky {
  top: 0;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
  flex-direction: row;
}

nav ul li {
  margin: 15px 15px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  font-weight: 300;
  transition: color 0.3s ease;
  list-style-type: none;
}

nav ul li a:hover {
  color: #c1440e;
  cursor: none;
}

/* Main content styles */
main {
  padding: 100px 0;
  color: #fff;
}

.main-content {
  margin: 0 auto;
  max-width: 1000px; /* Limit content width to 1000px */
}

.container {
  padding: 20px 30px;
}

h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  justify-content: center;
}
h3 {
  font-size: 24px;
  font-weight: 700;
  justify-content: center;
}

p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
}

ul li {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 10px;
}

ul li::before {
  margin-right: 5px;
}

/* Youtube Video styles */
.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  width: 100%;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

a {
  color: #007bff;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.3s ease;
}

a:hover {
  color: #0056b3;
  cursor: none;
}

/* Barchart styles */
.barchart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  color: #fff;
}

/* Footer styles */
.site-footer {
  background-image: linear-gradient(to bottom, #040C10 0%, #984B34 300%);
  text-align: center;
  color: white;
  width: 100%;
}

.site-footer h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  color: white;
}

.site-footer p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
}

.site-footer a:hover {
  color: white;
  cursor: none;
}

.download-button {
  margin-top: 10px;
  font-size: 20px;
  font-weight: 700;
  padding: 10px 20px;
  background-color: #c1440e; /* Change the background color as desired */
  color: #fff; /* Text color */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.download-button:hover {
  cursor: none;
  background-color: #cf5e2e; /* Change the background color on hover */
}

/* Adjust the icon size and spacing */
.download-button i {
  margin-right: 5px; /* Spacing between icon and text */
  font-size: 18px; /* Icon size */
}

.site-footer-social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
}

.site-footer-social:hover{
  cursor: none;
  color: white;
}

.site-footer-owner {
  font-size: 14px;
  color: #aaa;
}

.site-footer-owner a {
  color: #aaa;
}

/* Media Queries */
@media screen and (max-width: 600px) {
  .container {
    margin: 10px;
    max-width: none;
    padding: 0;
  }
}

/* scrollbar styling */

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  height: 0px;
  width: 8px;
  border: 1px solid #fff;
}

::-webkit-scrollbar-track {
  border-radius: 0;
  background: #eeeeee;
}

::-webkit-scrollbar-thumb {
  border-radius: 0;
  background: #b0b0b0;
}

/* Add animation classes for navigation menu items */
.nav-item {
  transition: color 0.3s ease, transform 0.3s ease;
}
.nav-item.active {
  color: #007bff; /* Change the color to highlight the active item */
  transform: scale(1.1); /* Apply a scale effect */
}

/* Add animation classes for containers */
.container {
  margin: 0 auto;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}
.container.active {
  opacity: 1;
  transform: translateY(0);
}