.project-link {
  text-decoration: none; /* Remove underlines from links */
}

.project-card {
  position: relative; /* Ensure proper stacking of text and rectangle */
  padding: 20px; /* Padding inside the card */
  border-radius: 8px; /* Rounded corners */
  background-color: transparent; /* Initially, the background is transparent */
  transition: background-color 0.5s ease; /* Smooth transition for background */
}

.project-link:hover .project-card {
  background-color: #984B34 /* Background color on parent link hover */
}

.project-title {
  color: #fff;
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.project-description {
  color: #fff;
  font-size: 1rem;
  margin-bottom: 10px;
}

.project-tech-container {
  flex-direction: row;
  width: 100%;
  display: flex;
  justify-content: flex-start;
}

.project-tech {
  font-size: 0.9rem;
  color: white;
  display: inline-block;
  padding: 5px 10px;
  background-color: #1C2951;
  border: 1px solid #333;
  border-radius: 10px;
  margin-right: 5px; /* Adjust spacing between buttons */
  cursor: default; /* Prevent click behavior */
}